import { Component } from "react";
import Header from "../Header";
import { Redirect } from "react-router-dom";
import "./style.css";

const question = [
  { id: 1, question: "seven(times(five()))" },
  { id: 2, question: "four(plus(nine()))" },
  { id: 3, question: "eight(minus(three()))" },
  { id: 4, question: "six(divided_by(two()))" },
  { id: 5, question: "eight(divided_by(three()))" },
];

const QuestionTag = (props) => {
  const { details, onClickquestion, activeQuestion } = props;
  const { id } = details;

  const onClickTag = () => {
    onClickquestion(id);
  };
  return (
    <li
      onClick={onClickTag}
      className={`question-tag ${id === activeQuestion ? "active-ques" : ""}`}
    >
      {id}
    </li>
  );
};

const ans = [];

class MasterPage extends Component {
  state = { activeQuestion: 1, ans: [], response: "" };

  onClickquestion = (id) => {
    this.setState((prevState) => ({
      activeQuestion: id,

      response: "",
    }));
  };

  onSaveAns = () => {
    const { activeQuestion, response } = this.state;
    const x = { id: activeQuestion, response: response };

    ans[activeQuestion - 1] = x;
    this.setState((prevState) => ({
      activeQuestion: Math.min(prevState.activeQuestion + 1, question.length),
      response: "",
    }));
  };

  onSubmitTest = () => {
    alert("You have successfully submitted test!");
    localStorage.setItem("student_res", JSON.stringify(ans));
    localStorage.removeItem("Login_token");
    this.setState({ x: "" });
  };

  onChangeRes = (e) => {
    this.setState((prevState) => ({
      response: e.target.value,
    }));
  };

  render() {
    const { response, activeQuestion } = this.state;
    const jwtToken = localStorage.getItem("Login_token");
    if (jwtToken === null) {
      return <Redirect to="/login" />;
    }
    return (
      <>
        <Header />
        <ul className="question-slider">
          {question.map((each) => (
            <QuestionTag
              details={each}
              onClickquestion={this.onClickquestion}
              activeQuestion={activeQuestion}
            />
          ))}
        </ul>
        <div className="question-cont">
          <p>Question: {activeQuestion}</p>
          <hr className="hr" />
          <p>{`Compute the given Expression:`}</p>
          <p className="question">{question[activeQuestion - 1].question}</p>
          <p>Enter Answer:</p>
          <input
            className="ans-input"
            type="text"
            value={response}
            onChange={this.onChangeRes}
          />

          <div className="sub-btn">
            <button
              className="submitTestBtn"
              onClick={this.onSaveAns}
            >
              Save
            </button>

            <button
              className="submitTestBtn"
              onClick={this.onSubmitTest}
            >
              Submit Test
            </button>
          </div>
        </div>
      </>
    );
  }
}

export default MasterPage;
