import React, { Component } from "react";
import { Redirect } from "react-router-dom";
import {
  LoginMainCounter,
  LoginFormContainer,
  LoginHead,
  VquizHead,
} from "./styledComponents";
import UserContext from "../../context/UserContext";
import "./style.css";

class LoginForm extends Component {
  state = { username: "", password: "" };

  render() {
    const { username, password } = this.state;

    const jwtToken = localStorage.getItem("Login_token");
    if (jwtToken !== null) {
      return <Redirect to="/" />;
    }

    return (
      <LoginMainCounter>
        <VquizHead>V-Quiz</VquizHead>
        <LoginFormContainer>
          <UserContext.Consumer>
            {(value) => {
              const { activeUser, changeUser } = value;
              const OnClickBtn = (e) => {
                console.log(e.target.textContent);
                changeUser(e.target.textContent);
              };

              const onChangeUsername = (e) => {
                this.setState({ username: e.target.value });
              };

              const onChangePassword = (e) => {
                this.setState({ password: e.target.value });
              };

              const onSubmitForm = (e) => {
                const { history } = this.props;
                e.preventDefault();
                if (
                  activeUser === "Master" &&
                  username === "teacher" &&
                  password === "teacher@123"
                ) {
                  localStorage.setItem("Login_token", "Master");
                  history.replace("/");
                } else if (
                  activeUser === "Student" &&
                  username === "student" &&
                  password === "student@123"
                ) {
                  localStorage.setItem("Login_token", "Student");
                  history.replace("/");
                } else {
                  console.log("fail");
                }
              };

              return (
                <>
                  <div>
                    <LoginHead>Log in for:</LoginHead>
                    <button className="login-master-btn" onClick={OnClickBtn}>
                      Master
                    </button>
                    <button className="login-master-btn" onClick={OnClickBtn}>
                      Student
                    </button>
                  </div>
                  <div className="login-form-cont">
                    <h1>{activeUser} Login</h1>
                    <form onSubmit={onSubmitForm}>
                      <div className="login-username-sec">
                        <label htmlFor="username">USERNAME</label> <br />
                        <input
                          className="login-input-field"
                          id="username"
                          type="text"
                          onChange={onChangeUsername}
                        />
                      </div>
                      <div className="login-password-sec">
                        <label htmlFor="password">PASSWORD</label> <br />
                        <input
                          className="login-input-field"
                          id="password"
                          type="password"
                          onChange={onChangePassword}
                        />
                      </div>
                      <button className="login-btn" type="submit">
                        Log In
                      </button>
                    </form>
                  </div>
                </>
              );
            }}
          </UserContext.Consumer>
        </LoginFormContainer>
      </LoginMainCounter>
    );
  }
}

export default LoginForm;
