import { Component } from "react";
import Header from "../Header";
import "./style.css";

const zero = (operation) => (!operation ? 0 : operation(0));
const one = (operation) => (!operation ? 1 : operation(1));
const two = (operation) => (!operation ? 2 : operation(2));
const three = (operation) => (!operation ? 3 : operation(3));
const four = (operation) => (!operation ? 4 : operation(4));
const five = (operation) => (!operation ? 5 : operation(5));
const six = (operation) => (!operation ? 6 : operation(6));
const seven = (operation) => (!operation ? 7 : operation(7));
const eight = (operation) => (!operation ? 8 : operation(8));
const nine = (operation) => (!operation ? 9 : operation(9));

const plus = (number) => (other_number) => other_number + number;
const minus = (number) => (other_number) => other_number - number;
const times = (number) => (other_number) => other_number * number;
const divided_by = (number) => (other_number) =>
  Math.floor(other_number / number);

const question = [
  { id: 1, question: "seven(times(five()))" },
  { id: 2, question: "four(plus(nine()))" },
  { id: 3, question: "eight(minus(three()))" },
  { id: 4, question: "six(divided_by(two()))" },
  { id: 5, question: "eight(divided_by(three()))" },
];

const QuestionTag = (props) => {
  const { details, onClickquestion, activeQuestion } = props;
  const { id } = details;

  const onClickTag = () => {
    onClickquestion(id);
  };
  return (
    <li
      onClick={onClickTag}
      className={`question-tag ${id === activeQuestion ? "active-ques" : ""}`}
    >
      {id}
    </li>
  );
};

class MasterPage extends Component {
  state = { activeQuestion: 1 };

  onClickquestion = (id) => {
    this.setState({ activeQuestion: id });
  };

  render() {
    const studentResponse = JSON.parse(localStorage.getItem("student_res"));
    const { activeQuestion } = this.state;
    return (
      <>
        <Header />
        <ul className="question-slider">
          {question.map((each) => (
            <QuestionTag
              key={each.id}
              details={each}
              onClickquestion={this.onClickquestion}
              activeQuestion={activeQuestion}
            />
          ))}
        </ul>
        <div className="question-cont">
          <p>Question: {activeQuestion}</p>
          <hr className="hr" />
          <p>{`Compute the given Expression:`}</p>
          <p className="question">{question[activeQuestion - 1].question}</p>
          <p>Answer:</p>
          <p className="answer">
            {eval(question[activeQuestion - 1].question)}
          </p>
        </div>
        <div className="act">
          <h1 className="res-head">Student Response</h1>
          <table className="activity-cont act">
            <tr className="each-response">
              <td className="quesno">Question No:</td>
              <td className="ques">Question</td>
              <td className="res">Response</td>
            </tr>
            {studentResponse.map((each) => (
              <tr
                key={each.id}
                className="each-response"
              >
                <td className="quesno">{each.id}</td>
                <td className="ques">{question[each.id - 1].question}</td>
                <td className="res">{each.response}</td>
              </tr>
            ))}
          </table>
        </div>
      </>
    );
  }
}

export default MasterPage;
