import styled from "styled-components";

export const LoginMainCounter = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: #8ec5fc;
  background-image: linear-gradient(62deg, #8ec5fc 0%, #e0c3fc 100%);
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  color: white;
  @media (max-width: 500px) {
    height: 100dvh;
    width: 100%;
    padding: 35px;

    flex-direction: column;
  }
`;

export const LoginFormContainer = styled.div`
  justify-self: center;
  margin-top: 100px;
  background: #fff;
  color: #000;
  display: flex;
  align-items: center;
  padding: 40px;
  border-radius: 15px;
  box-shadow: 21px 20px 38px -20px rgba(0, 0, 0, 0.75);
  @media (max-width: 500px) {
    flex-direction: column;
    margin-top: 60px;
  }
`;

export const LoginHead = styled.h1`
  font-size: 18px;
`;

export const VquizHead = styled.h1`
  font-size: 40px;
  color: black;
  font-weight: 900;
`;
